import { useState, useEffect } from "react";
import { Navigate, useLocation, useNavigate } from "react-router-dom";
function Loading() {
    const [quote, setQuote] = useState();
    const [remainingTime, setRemainingTime] = useState(10000);
    const { state } = useLocation();
    const navigate = useNavigate();
    // const [navigateBack, setNavigateBack] = useState(false);

    // let navigateBack;




    useEffect(() => {
        console.log(state);
        let quotes = ["First you take a drink, then the drink takes a drink, then the drink takes you. - F. Scott Fitzgerald", "Wer den Shot nicht ehrt, ist den Cocktail nicht wert.", "Wenn dir das Leben Zitronen gibt, mach einen Cocktail draus.", "Trinke was klar ist, rede was wahr ist und schau vor dem Kacken ob Klopapier da ist.", "Reden ist Silber und trinken ist Gold.", "I have a drink - Martin Luther King Jr. oder so", "Was du trinkst macht dich frei, kommt es wieder heraus bleibt draussen für immer. - Konfuzius oder so", "Man muss trinken bevor man saufen kann.", "Zitat lädt - Raspberry Pi"];
        // setRemainingTime(state.remainingTime);
        setQuote(quotes[Math.floor(Math.random() * quotes.length)]);
        // setTimeout(() => { setRemainingTime(remainingTime - 1000) }, 1000);
        setTimeout(() => { navigate("/") }, remainingTime);
    }, []);

    // useEffect(() => {
    //     if (remainingTime > 0) {
    //         // navigateBack = <></>
    //         setTimeout(() => { setRemainingTime(remainingTime - 1000) }, 1000);
    //     } else {
    //         navigate("/");
    //         // navigateBack = <Navigate to="/" replace={true} />
    //     }
    // }, [remainingTime])

    return (
        <div className="frontend-container">
            {/* {navigateBack ? <Navigate to="/" replace={true} />:<></>} */}
            {/* {navigateBack} */}
            <div className="left">

                {/* {navigateBack ? <p>Hallooo</p> : <></>} */}
                <div className="top">
                    <p className="quote">{quote}</p>
                </div>
                {/* <div>
                    <p><strong>Verbleibende Zeit: </strong>{
                        Math.round(remainingTime / 1000)
                    } s</p>
                </div> */}
            </div>
            {state.drink ?
                <div className="right">
                    <h1>{state.drink.name}</h1>
                    <p><strong>Zutaten: </strong>{state.baseDrinkNames.join(", ")}</p>
                </div>
                : <></>}
        </div>)
}

export default Loading;